<template>
	<div id="store-basic">
		<public-header title="基本信息"></public-header>
		<div class="content">
			<van-cell-group>
				<van-cell title="门店名称" :value="store.title ? store.title : '未设置'" is-link :to="util.getUrl({path: '/pages/merchant/store/update', query: {type: 'title', id: id}})" />
				<van-cell class="store-logo flex-lr" title="门店Logo" :to="util.getUrl({path: '/pages/merchant/store/update', query: {type: 'logo', id: id}})">
					<div slot="right-icon" class="flex ">
						<img :src="store.logo" alt="" v-if="store.logo">
						<span v-else>未设置</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-cell title="门店描述" :value="store.content ? store.content : '未设置'" is-link :to="util.getUrl({path: '/pages/merchant/store/update', query: {type: 'content', id: id}})" />
				<van-cell title="所属分类" :value="store.category ? store.category : '未设置'" is-link :to="util.getUrl({path: '/pages/merchant/store/update', query: {type: 'cid', id: id}})" />
				<van-cell title="营业时间" :value="(store.business_hours && store.business_hours.length > 0) ? store.business_hours_cn : '未设置'" is-link :to="util.getUrl({path: '/pages/merchant/store/update', query: {type: 'business_hours', id: id}})"
				/>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell title="联系电话" :value="store.telephone ? store.telephone : '未设置'" is-link :to="util.getUrl({path: '/pages/merchant/store/update', query: {type: 'telephone', id: id}})" />
				<van-cell title="详细地址" :value="store.address ? store.address : '未设置'" is-link :to="util.getUrl({path: '/pages/merchant/store/update', query: {type: 'address', id: id}})" />
				<van-cell title="门店实景" :value="(store.thumbs && store.thumbs.length > 0) ? '已上传' + store.thumbs.length + '张' : '待上传'" is-link :to="util.getUrl({path: '/pages/merchant/store/update', query: {type: 'thumbs', id: id}})" />
			</van-cell-group>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				id: 0,
				store: {
					title: '',
					logo: '',
					content: '', //描述
					category: '',
					business_hours_cn: '',
					telephone: '',
					address: '',
					thumbs: []
				},
				showPreLoading: true
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					url: 'plateform/store/shop/setting',
					vue: that,
					autoAssign: true,
					variable: 'store',
					data: {
						id: that.id
					}
				});
			}
		},
		created() {
			if(this.$route.query && this.$route.query.id > 0) {
				this.id = parseInt(this.$route.query.id)
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#store-basic .van-cell.store-logo img{
		width: 80px;
		height: 60px;
		border-radius: 3px;
	}
</style>


